            },
            frameContent: function () {
                if (this.content) {
                    return this.content;
                } 
                return this.iframe.src;                
            }
        },
        mounted() {
            this.loaded = true;
        },
        methods: {
            onFrameLoad: async function (frame) {
                await this.$nextTick()

                let head = $(this.$refs.frame_result).contents().find("head");
                let css = '<style type="text/css">body{overflow:hidden;} .watermark{-webkit-user-select: none;-khtml-user-select: none;-moz-user-select: none;-o-user-select: none;user-select: none;color:#d0d0d0;font-size:70pt;opacity: 0.05;-webkit-transform:rotate(-45deg);-moz-transform:rotate(-45deg);position:absolute;width:100%;margin:0;z-index:-1;left:20px;top:100px;}</style>';
                $(head).append(css);

                //let td = $(this.$refs.frame_result).contents().find('td:first-child:first');
                //$(td).append($('<img>', {'width': '100', 'src':'/images/logo1.png', 'alt':''}));

                let body = $(this.$refs.frame_result).contents().find("body");
                let watermark = '<div class="watermark"><img alt="" draggable="false" oncontextmenu="return false" onmousedown="return false;" ondragstart="return false;" src="' + window._root  +'images/logo1.png" /></div>';
                $(body).prepend(watermark);
            },
            printResult: function () {
                //var iFrameBody = this.$refs.frame_result.contentWindow.document;
                this.$refs.frame_result.contentWindow.print();
            }
